import React from 'react';

const SectionFeedback = ({ section, feedback, score }) => {
  const sectionNames = {
    contact: 'Contact Information',
    summary: 'Professional Summary',
    experience: 'Work Experience',
    education: 'Education',
    skills: 'Skills'
  };

  const sectionIcons = {
    contact: '??',
    summary: '??',
    experience: '??',
    education: '??',
    skills: '???'
  };

  const getScoreColor = (score) => {
    if (score >= 0.8) return 'text-green-600 bg-green-50';
    if (score >= 0.6) return 'text-yellow-600 bg-yellow-50';
    return 'text-red-600 bg-red-50';
  };

  const getScoreLabel = (score) => {
    if (score >= 0.8) return 'Excellent';
    if (score >= 0.6) return 'Good';
    if (score >= 0.4) return 'Fair';
    return 'Needs Work';
  };

  const percentage = Math.round(score * 100);

  return (
    <div className="bg-white rounded-xl shadow-md p-6 border-l-4 border-blue-500 hover:shadow-lg transition-shadow">
      <div className="flex items-start justify-between mb-4">
        <div className="flex items-center gap-3">
          <span className="text-3xl">{sectionIcons[section]}</span>
          <div>
            <h3 className="text-lg font-bold text-gray-900">{sectionNames[section]}</h3>
            <span className={`text-xs font-semibold px-2 py-1 rounded-full ${getScoreColor(score)}`}>
              {getScoreLabel(score)}
            </span>
          </div>
        </div>
        <div className="text-right">
          <div className={`text-2xl font-bold ${getScoreColor(score).split(' ')[0]}`}>
            {percentage}%
          </div>
          <div className="text-xs text-gray-500">Score</div>
        </div>
      </div>

      {/* Progress Bar */}
      <div className="w-full bg-gray-200 rounded-full h-2 mb-4">
        <div 
          className={`h-2 rounded-full transition-all duration-500 ${
            score >= 0.8 ? 'bg-green-500' : 
            score >= 0.6 ? 'bg-yellow-500' : 
            'bg-red-500'
          }`}
          style={{ width: `${percentage}%` }}
        />
      </div>

      {/* Feedback Text */}
      {feedback ? (
        <p className="text-gray-700 leading-relaxed">{feedback}</p>
      ) : (
        <p className="text-gray-400 italic">No feedback available for this section.</p>
      )}
    </div>
  );
};

export default SectionFeedback;